.{
margin:0;
padding:0;
box-sizing: border-box;
font-family: 'Poppins', sans-serif;
}

body {
    background: #081b29;
    color: #ededed;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 20px 10%;
    background: transparent;
    display: flex;
    align-items: center;
    z-index: 100px;
    word-spacing: 20px;
}

.logo{
    position: relative;
    font-size: 25px;
    color: #ededed;
    text-decoration: none;
    font-weight: 600;
    margin-right: 50%;

}

.logo::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 4.6s;

}


.navbar a{
    font-size: 18px;
    color: #ededed;
    text-decoration: none;
    font-weight: 500;
    margin-left: 35;
    transition: .3s;
    
}

.navbar a::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 60%;
    height: 110%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 7.5s;
    z-index: 2;
}

.navbar a:hover,
.navbar a.active {
    color: #00abf0;
}

.home{
    height: 100vh;
    background: url('devPeople.png');
    background-position: right;
    background-size: 50%;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    padding: 0 10%;
}

.home-content {
    max-width: 600px;
}

.home-content h1 {
    position:relative;
    font-size: 56px;
    font-weight: 700;
    line-height: 1.2;
}

.home-content h1::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 5.5s;

}

.home-content h3{
    position: relative;
    font-size: 32px;
    font-weight: 700;
    color: #00abf0
}

.home-content h3::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 5.8s;

}

.home-content p{
    position: relative;
    font-size: 19px;
    margin: 20px 0 40px;
    justify-content: left;
    line-height: 1.7;
}

.home-content p::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 6.1s;
}

.home-content   .btn-box{
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 345px;
    height: 50px;

}

.home-content .btn-box::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 110%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 6.4s;
    z-index: 3;
}

.btn-box a {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 100%;
    background: #00abf0;
    border: 2px solid #00abf0;
    border-radius: 8px;
    font-size: 19px;
    color: #081b29;
    text-decoration: none;
    font-weight: 600;
    letter-spacing: 1px;
    z-index: 1;
    overflow: hidden;
    transition: .5s;
}

.btn-box a:hover {
    color:#00abf0;
}


.btn-box a:nth-child(2){
    background: transparent;
    color: #00abf0;
}

.btn-box a:nth-child(2):hover{
    color: #081b29;
    
}

.btn-box a:nth-child(2)::before {
    background-color: #00abf0;
}

.btn-box a::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #081b29;
    z-index: -1;
    transition: .5s;
}

.btn-box a:hover::before {
        width: 100%;
}

.home-sci {
    position: absolute;
    bottom: 40px;
    width: 170px;
    display: flex;
    justify-content: space-between;
    margin-top: 5%;
    
}

.home-sci::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 110%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 7s;
    z-index: 3;
}

.home-sci a {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background: transparent;
    border: 2px solid #00abf0;
    border-radius: 50%;
    font-size: 20px;
    color: #00abf0;
    text-decoration: none;
    z-index: 1;
    overflow: hidden;
    transition: .5s;
}


.home-sci a:hover {
            color: #081b29;
}

.home-sci a::before  {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background:#00abf0;
    color:#00abf0;
    z-index: -1;
    transition: .5s;
}

.home-sci a:hover::before {
    width: 100%;
}

.home-imgHover{
    position: absolute;
    top: 0;
    right: .4%;
    width: 40%;
    height:60%;
    margin-top: 10%;
    background: transparent;
    transition: 3s;
}

.home-imgHover:hover{
    background: #081b29;
    opacity: .8;
}

.home-imgHover::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 110%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 7.5s;
    z-index: 2;
}

@keyframes showRight{
    100% {
        width: 0;
    }
}

/* Existing styles */

/* Add this media query for screens smaller than 768px (typical smartphones) */
@media (max-width: 768px) {
    .header {
        padding: 10px 5%;
        /* Reduce padding for smaller screens */
    }

    .logo {
        font-size: 20px;
        /* Decrease font size for smaller screens */
        margin-right: 10px;
        /* Adjust margin */
    }

    .navbar a {
        font-size: 16px;
        /* Decrease font size for smaller screens */
    }

    .home-content h1 {
        font-size: 36px;
        /* Decrease font size for smaller screens */
    }

    .home-content h3 {
        font-size: 24px;
        /* Decrease font size for smaller screens */
    }

    .home-content p {
        font-size: 16px;
        /* Decrease font size for smaller screens */
        margin: 10px 0 20px;
        /* Adjust margin */
    }

    .btn-box {
        width: 250px;
        /* Adjust button box width */
    }

    .btn-box a {
        font-size: 16px;
        /* Decrease font size for smaller screens */
        width: 120px;
        /* Adjust button width */
    }

    .home-sci {
        width: 120px;
        /* Adjust social media icons width */
        bottom: 20px;
        /* Adjust bottom margin */
    }
}